.layout {
  position: fixed;
  right: 0;
  left: auto;
  height: 100%;
  width: fit-content;
  padding: 15px;
  top: 0;
  flex: 1 0 auto;
  overflow-y: auto;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 100;
}

.close:hover {
  font-weight: 700;
  cursor: pointer;
  color: inherit;
}
